@font-face {
  font-family: "PTMono-Regular";
  src: url("../fonts/PTMono-Regular.woff2"),
    url("../fonts/PTMono-Regular.woff");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: "RobotoCondensed-Regular";
  src: url("../fonts/RobotoCondensed-Regular.woff2"),
    url("../fonts/RobotoCondensed-Regular.woff");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: "RobotoCondensed-Bold";
  src: url("../fonts/RobotoCondensed-Bold.woff2"),
    url("../fonts/RobotoCondensed-Bold.woff");
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: "Roboto-Medium";
  src: url("../fonts/Roboto-Medium.woff2"),
    url("../fonts/Roboto-Medium.woff");
  font-weight: 500;
  font-style: normal;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  box-sizing: border-box;
}

body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: "PTMono-Regular", "Arial", sans-serif;
  font-size: 16px;
  line-height: 22px;
  font-weight: 400;
  color: #666666;
}

