.about-company {
  max-width: 1245px;
  margin: 0 auto;
  padding: 0 20px 110px 20px;
  position: relative;

  &::before {
    width: 500px;
    height: 570px;
    position: absolute;
    content: " ";
    top: -180px;
    left: -408px;
    transform: rotate(60deg);
    background-image: url("../img/advantages-img-dotted@1x.png");
    background-repeat: no-repeat;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/advantages-img-dotted@2x.png");
      background-size: 500px 570px;
    }
  }

  &::after {
    width: 500px;
    height: 570px;
    position: absolute;
    content: " ";
    top: -551px;
    left: 39.3%;
    transform: rotate(0deg);
    background-image: url("../img/advantages-img-dotted@1x.png");
    background-repeat: no-repeat;
    z-index: -1;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/advantages-img-dotted@2x.png");
      background-size: 500px 570px;
    }
  }
}

.about-company__wrapper {
  padding: 0 36px 0 198px;
  position: relative;

  &::before {
    position: absolute;
    content: "2005";
    top: 53px;
    left: -79px;
    font-family: $additional-fonts-bold;
    font-size: 156px;
    line-height: 220px;
    transform: rotate(-90deg);
    background-image: url("../img/desktop-about-mask-text@1x.png");
    background-repeat: no-repeat;
    background-clip: text;
    -webkit-text-fill-color: transparent;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/desktop-about-mask-text@2x.png");
      background-size: 860px 484px;
    }
  }
}

.about-company__wrapper h2 {
  margin: 0;
  padding-bottom: 21px;
  font-family: $additional-fonts;
  font-size: 36px;
  line-height: 51px;
  font-weight: 400;
}

.about-company__wrapper p {
  margin: 0;
  padding-bottom: 21px;

  &:nth-child(3n) {
    padding-bottom: 38px;
  }
}

/*----- Планшетная версия -----*/

@media (max-width: $tablet-max-width) {
  .about-company {
    width: auto;
    padding: 28px 25px 83px 25px;

    &::before {
      display: none;
    }

    &::after {
      top: -89.5%;
      left: 62.4%;
    }
  }

  .about-company__wrapper {
    padding: 0 0 0 187px;
    padding-right: 0;
    padding-left: 187px;

    &::before {
      font-size: 178px;
      line-height: 251px;
      top: 52px;
      left: -110px;
      background-image: url("../img/tablet-about-mask-text@1x.png");

      @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
        background-image: url("../img/tablet-about-mask-text@2x.png");
        background-size: 982px 553px;
      }
    }
  }

  .about-company__wrapper h2 {
    font-size: 32px;
    line-height: 45px;
  }

  .about-company__wrapper p {
    &:nth-child(3n) {
      padding-bottom: 43px;
    }
  }

  .about-company__wrapper p span {
    display: none;
  }
}

/*----- Мобильная версия -----*/

@media (max-width: $mobile-max-width) {
  .about-company {
    padding: 36px 15px 65px 15px;

    &::after {
      top: -71%;
      left: 46%;
    }
  }

  .about-company__wrapper {
    padding-left: 0;

    &::before {
      display: none;
    }
  }

  .about-company__wrapper h2 {
    font-size: 28px;
    line-height: 39px;
  }

  .about-company__wrapper p {
    padding-bottom: 40px;

    &:nth-child(3n) {
      padding-bottom: 29px;
    }
  }
}
