.site-list {
  @include mixin-list-reset;
  display: flex;
  flex-wrap: wrap;
  flex-grow: 1;
  justify-content: space-around;
}

.site-list__item {
  &:last-child {
    margin-right: 8px;
  }
}

.site-list__item-link {
  display: flex;
  align-items: center;
  text-decoration: none;

  &:hover {
    opacity: 0.7;
  }

  &:active {
    opacity: 0.4;
  }
}

.site-list__icon-mail {
  width: 19px;
  height: 14px;
}

.site-list__icon-phone {
  width: 18px;
  height: 18px;
}

.site-list .site-list__item span {
  padding-left: 19px;
  color: $additional-text-color;
}

/*----- Планшетная версия -----*/

@media (max-width: $tablet-max-width) {
  .site-list {
    justify-content: flex-end;
  }

  .site-list__item {
    margin-right: 32px;

    &:last-child {
      margin-right: 0;
    }
  }

  .site-list__icon-mail {
    width: 25px;
    height: 18px;
    margin-top: 3px;
  }

  .site-list__icon-phone {
    width: 24px;
    height: 24px;
    margin-bottom: 8px;
  }
}

/*----- Мобильная версия -----*/

@media (max-width: $mobile-max-width) {
  .site-list__item {
    margin-right: 24px;
  }

  .site-list__icon-phone {
    margin-bottom: 4px;
  }
}

/*----- Замыкание стилей на определённой ширине -----*/

@media (max-width: $tablet-width-only) {
  .site-list .site-list__item span {
    display: none;
  }
}
