.main-footer__wrapper {
  max-width: 1450px;
  margin: 0 auto;
  padding: 65px 20px 0 20px;
  display: flex;
}

.main-footer__box {
  width: 29.8%;
  display: flex;
}

.main-footer__img-box {
  margin-bottom: 50px;
}

.main-footer__logo-box {
  width: 36.2%;
  flex-grow: 1;
}

.main-footer__logo-box p {
  margin: 0;
  padding-left: 30px;
  font-size: 13px;
  line-height: 24px;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  display: none;
}

.main-footer__logo {
  display: flex;
  text-decoration: none;
}

.main-footer__text-block {
  padding: 8px 0 0 14px;
}

.main-footer__img-box .main-footer__text-block p {
  margin: 0;
  padding: 0;
  font-family: $additional-fonts-bold;
  font-size: 25px;
  line-height: 25px;
  text-transform: initial;
  letter-spacing: 0;
  display: block;
  color: #203c42;
}

.main-footer__text-block span {
  margin: 0;
  font-family: $additional-fonts-medium;
  font-size: 12px;
  line-height: 14px;
  color: #788891;
}

.main-footer__menu-inner {
  display: flex;
}

.main-footer__menu-box {
  width: 72%;
  min-height: 200px;
  flex-grow: 1;
  position: relative;
}

.main-footer__menu-box h3 {
  margin: 0;
  padding-bottom: 25px;
  font-family: $additional-fonts;
  font-size: 15px;
  line-height: 16px;
  font-weight: 400;
  letter-spacing: 1px;
  text-transform: uppercase;
  color: $additional-background-color;
}

.main-footer__btn-closed {
  display: none;
}

.main-footer__menu-wrapper {
  width: 70.2%;
}

.main-footer__address-box {
  width: 36.7%;
  position: relative;
}

.main-footer__address-box h3 {
  margin: 0;
  padding-bottom: 25px;
  font-family: $additional-fonts;
  font-size: 15px;
  line-height: 16px;
  font-weight: 400;
  letter-spacing: 1px;
  text-transform: uppercase;
  color: $additional-background-color;
}

.main-footer__btn {
  display: none;
}

/*----- Планшетная версия -----*/

@media (max-width: $tablet-max-width) {
  .main-footer__wrapper {
    padding: 0;
    display: flex;
    flex-direction: column;
  }

  .main-footer__box {
    width: auto;
    padding: 0 25px;
    flex-direction: column;
    border-bottom: solid 1px rgba(98, 98, 98, 0.2);
  }

  .main-footer__logo-box {
    width: 100%;
    padding: 19px 0 22px 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .main-footer__logo-box p {
    margin: 0;
    padding-left: 15px;
    font-size: 13px;
    line-height: 24px;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    display: block;
  }

  .main-footer__img-box {
    margin-bottom: 0;
  }

  .main-footer__icon-logo {
    margin-left: 0;
  }

  .main-footer__menu-wrapper {
    width: auto;
    border-bottom: solid 1px rgba(98, 98, 98, 0.2);
  }

  .main-footer__inner {
    width: auto;
    display: block;
  }

  .main-footer__menu-inner {
    width: auto;
    padding: 38px 25px 36px 25px;
  }

  .main-footer__menu-box {
    width: 52.5%;
    min-height: 0;
    flex-grow: 0;
  }

  .main-footer__address-box {
    width: auto;
  }
}

/*----- Мобильная версия -----*/

@media (max-width: $mobile-max-width) {
  .main-footer__box {
    padding: 0 15px;
  }

  .main-footer__logo-box {
    padding-top: 46px;
    padding-bottom: 48px;
    flex-direction: column;
  }

  .main-footer__logo-box p {
    padding: 22px 0 32px 0;
  }

  .main-footer__menu-inner {
    padding: 0;
    flex-direction: column;
  }

  .main-footer__menu-box {
    width: auto;
    padding: 0 15px;
  }

  .main-footer__menu-box h3 {
    padding: 29px 0 28px 0;
  }

  .main-footer__menu-box a {
    display: block;
    text-decoration: none;
  }

  .main-footer__btn {
    width: 16px;
    height: 16px;
    position: absolute;
    content: " ";
    top: 30px;
    right: 17px;
    background-color: transparent;
    border: none;
    display: block;

    &::before {
      width: 16px;
      height: 1px;
      position: absolute;
      content: " ";
      top: 7px;
      left: 0;
      transform: rotate(-90deg);
      background-color: #c4c4c4;
    }

    &::after {
      width: 16px;
      height: 1px;
      position: absolute;
      content: " ";
      top: 7px;
      left: 0;
      background-color: #c4c4c4;
    }
  }

  .main-footer__address-box {
    padding: 0 18px 0 15px;
    border-top: solid 1px rgba(98, 98, 98, 0.2);
  }

  .main-footer__address-box h3 {
    padding: 28px 0 31px 0;
  }

  .main-footer__address-box a {
    display: block;
    text-decoration: none;
  }

  .main-footer__btn--active {
    &::before {
      width: 0;
      transform: rotate(0deg);
    }

    &::after {
      width: 16px;
      height: 1px;
      position: absolute;
      content: " ";
      top: 7px;
      left: 0;
      background-color: #c4c4c4;
    }
  }
}
