.modal {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 5;
  display: none;
}

.modal__wrapper {
  width: 490px;
  padding: 31px 40px 31px 40px;
  position: fixed;
  top: 14.3%;
  left: 37.3%;
  background-color: $base-background-color;
  animation: bounce 1s;
}

.modal__wrapper h2 {
  margin: 0;
  padding-bottom: 12px;
  font-family: $additional-fonts;
  font-size: 28px;
  line-height: 39px;
  font-weight: 400;
  color: $additional-text-color;
}

.modal__wrapper p {
  width: 95%;
  margin: 0;
  padding-bottom: 32px;
  color: $additional-text-color;
}

.modal__form {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}

.modal__form-box,
.modal__form-user-box {
  display: flex;
  flex-direction: column;
}

.modal__form-box input {
  margin-bottom: 31px;
  padding: 12px 12px 18px 24px;
  font-family: $base-fonts;
  font-size: 16px;
  line-height: 22px;
  background-color: rgba(255, 255, 255, 0.1);
  border: none;
  color: $additional-text-color;
  border-radius: 0;
  -webkit-appearance: none;

  &::placeholder {
    color: $additional-text-color;
  }
}

.modal__form-box textarea {
  min-height: 100px;
  margin-bottom: 34px;
  padding: 9px 25px;
  font-family: $base-fonts;
  font-size: 16px;
  line-height: 22px;
  border: none;
  color: $additional-text-color;
  background-color: rgba(255, 255, 255, 0.1);
  resize: none;
  overflow: auto;
  border-radius: 0;
  -webkit-appearance: none;

  &::placeholder {
    color: $additional-text-color;
  }
}

.modal__form label {
  margin-top: 35px;
  padding: 0 1px 5px 49px;
  font-size: 14px;
  line-height: 16px;
  color: rgba(255, 255, 255, 0.8);
  position: relative;
  cursor: pointer;

  &::before {
    width: 16px;
    height: 16px;
    position: absolute;
    content: " ";
    top: 0;
    left: 0;
    border: solid 1px $additional-text-color;
  }

  &:hover {
    opacity: 0.8;
  }
}

.modal__form-user-box input[type="checkbox"]:focus + label {
  outline: rgb(59, 153, 252) auto 5px;
}

.modal__form-user-box input:checked + label::after {
  width: 8px;
  height: 5px;
  position: absolute;
  content: " ";
  top: 5px;
  left: 4px;
  background-image: url("../img/icon-tick.svg");
  background-repeat: no-repeat;
}

.modal__btn-close {
  width: 20px;
  height: 20px;
  position: absolute;
  content: " ";
  top: 18px;
  right: 19px;
  border: none;
  background-color: transparent;
  cursor: pointer;

  &::before {
    width: 23px;
    height: 1px;
    position: absolute;
    content: " ";
    top: 10px;
    left: -1px;
    transform: rotate(45deg);
    background-color: $additional-text-color;
  }

  &::after {
    width: 23px;
    height: 1px;
    position: absolute;
    content: " ";
    top: 10px;
    left: -1px;
    transform: rotate(-45deg);
    background-color: $additional-text-color;
  }

  &:hover {
    opacity: 0.5;
  }
}

@keyframes bounce {
  0% {
    transform: translateY(-2000px);
  }

  70% {
    transform: translateY(30px);
  }

  90% {
    transform: translateY(-10px);
  }

  100% {
    transform: translateY(0);
  }
}

@keyframes shake {
  0%,
  100% {
    transform: translateX(0);
  }

  10%,
  30%,
  50%,
  70%,
  90% {
    transform: translateX(-10px);
  }

  20%,
  40%,
  60%,
  80% {
    transform: translateX(10px);
  }
}

.modal--active {
  display: block;
}

.modal--error {
  animation: shake 1s;
}
