.service-list {
  @include mixin-list-reset;
  margin-left: 18px;
  display: flex;
  flex-wrap: wrap;
}

.service-list__item {
  width: 31.2%;
  margin-right: 30px;
  margin-bottom: 30px;
  position: relative;
  background-color: #666666;

  &:nth-child(3n) {
    margin-right: 0;
  }
}

.service-list__item a {
  text-decoration: none;
}

.service-list__item-box {
  min-height: 620px;
  position: relative;
  background-color: rgba(27, 45, 55, 0.4);

  &::before {
    width: 63px;
    height: 63px;
    position: absolute;
    content: " ";
    bottom: 0;
    right: 0;
    background-color: $base-background-color;
  }

  &::after {
    width: 8px;
    height: 10px;
    position: absolute;
    content: " ";
    bottom: 26px;
    right: 24px;
    background-image: url("../img/arrow-right.svg");
    background-repeat: no-repeat;
  }

  &:hover {
    background-color: rgba(27, 45, 55, 0.6);
  }

  &:active {
    background-color: rgba(27, 45, 55, 0.8);
  }
}

.service-list__item h3 {
  margin: 0;
  padding: 50px 44px 20px 44px;
  font-family: $additional-fonts;
  font-size: 20px;
  line-height: 30px;
  font-weight: 400;
  color: $additional-text-color;
}

.service-list__item--printed-board {
  background-image: url("../img/printed-board-desktop@1x.png");
  background-size: cover;
  background-repeat: no-repeat;
}

.service-list__item--electronic-board {
  background-image: url("../img/electronic-printed-board-desktop@1x.png");
  background-size: cover;
  background-repeat: no-repeat;
}

.service-list__item--led-strip {
  background-image: url("../img/led-strip-desktop@1x.png");
  background-size: cover;
  background-repeat: no-repeat;
}

@media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
  .service-list__item--printed-board {
    background-image: url("../img/printed-board-desktop@2x.png");
  }

  .service-list__item--electronic-board {
    background-image: url("../img/electronic-printed-board-desktop@2x.png");
  }

  .service-list__item--led-strip {
    background-image: url("../img/led-strip-desktop@2x.png");
  }
}

/*----- Планшетная версия -----*/

@media (max-width: $tablet-max-width) {
  .service-list {
    margin-left: 1px;
  }

  .service-list__item {
    width: 100%;
    margin-bottom: 46px;
    margin-right: 0;
  }

  .service-list__item h3 {
    padding-top: 40px;
  }

  .service-list__item-box {
    width: auto;
    min-height: 256px;
  }

  .service-list__item--printed-board {
    background-image: url("../img/printed-board-tablet@1x.png");
  }

  .service-list__item--electronic-board {
    background-image: url("../img/electronic-printed-board-tablet@1x.png");
  }

  .service-list__item--led-strip {
    background-image: url("../img/led-strip-tablet@1x.png");
  }

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    .service-list__item--printed-board {
      background-image: url("../img/printed-board-tablet@2x.png");
    }

    .service-list__item--electronic-board {
      background-image: url("../img/electronic-printed-board-tablet@2x.png");
    }

    .service-list__item--led-strip {
      background-image: url("../img/led-strip-tablet@2x.png");
    }
  }
}

/*----- Мобильная версия -----*/

@media (max-width: $mobile-max-width) {
  .service__wrapper {
    padding: 0 15px 34px 15px;
  }

  .service-list__item {
    margin-bottom: 30px;
  }

  .service-list__item h3 {
    padding: 20px 34px;
  }

  .service-list__item--printed-board {
    background-image: url("../img/printed-board-mobile@1x.png");
  }

  .service-list__item--electronic-board {
    background-image: url("../img/electronic-printed-board-mobile@1x.png");
  }

  .service-list__item--led-strip {
    background-image: url("../img/led-strip-mobile@1x.png");
  }

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    .service-list__item--printed-board {
      background-image: url("../img/printed-board-mobile@2x.png");
    }

    .service-list__item--electronic-board {
      background-image: url("../img/electronic-printed-board-mobile@2x.png");
    }

    .service-list__item--led-strip {
      background-image: url("../img/led-strip-mobile@2x.png");
    }
  }
}
