.copyright-box {
  margin-top: -17px;
  border-top: solid 1px rgba(98, 98, 98, 0.2);
}

.copyright-box__wrapper {
  max-width: 1450px;
  margin: 0 auto;
  padding: 15px 20px;
  display: flex;
}

.copyright-box__wrapper p {
  margin: 0;
  font-size: 13px;
  line-height: 24px;
  text-transform: uppercase;
  letter-spacing: 0.6px;
  color: rgba(27, 45, 55, 0.5);
}

.copyright-box__law p {
  &:first-child {
    width: 220px;
  }

  &:last-child {
    width: 330px;
  }
}

.copyright-box__law {
  width: 42.3%;
  display: flex;
}

.copyright-box__privacy {
  width: 58%;
  display: flex;
  justify-content: space-between;
}

.copyright-box__privacy a {
  font-size: 13px;
  line-height: 24px;
  text-decoration: none;
  text-transform: uppercase;
  letter-spacing: 0.6px;
  color: rgba(27, 45, 55, 0.5);

  &:hover {
    color: rgba(27, 45, 55, 0.2);
  }

  &:active {
    color: rgba(27, 45, 55, 0.1);
  }
}

/*----- Планшетная версия -----*/

@media (max-width: $tablet-max-width) {
  .copyright-box {
    margin: 0;
    border-top: none;
  }

  .copyright-box__wrapper {
    width: auto;
    padding: 22px 25px;
  }

  .copyright-box__law {
    display: none;
  }

  .copyright-box__privacy {
    width: 100%;
  }
}

/*----- Мобильная версия -----*/

@media (max-width: $mobile-max-width) {
  .copyright-box__wrapper {
    padding: 27px 15px;
  }

  .copyright-box__privacy {
    flex-direction: column;
    align-items: center;
  }

  .copyright-box__privacy a {
    margin-bottom: 22px;
  }
}
