.service {
  max-width: 1245px;
  margin: 0 auto;
  padding: 0 20px 97px 20px;
}

.service h2 {
  margin: 0;
  padding: 88px 18px 52px 18px;
  font-family: $additional-fonts;
  font-size: 36px;
  line-height: 51px;
  font-weight: 400;
  color: $additional-text-color;
}

/*----- Планшетная версия -----*/

@media (max-width: $tablet-max-width) {
  .service {
    width: auto;
    padding: 0 25px 32px 25px;
    padding-bottom: 32px;
  }

  .service h2 {
    padding: 51px 91px 48px 0;
    font-size: 32px;
    line-height: 45px;
  }
}

/*----- Мобильная версия -----*/

@media (max-width: $mobile-max-width) {
  .service {
    padding: 0 15px 34px 15px;
  }

  .service h2 {
    padding: 50px 0 42px 10px;
    font-size: 28px;
    line-height: 39px;
  }
}
