.user-form {
  overflow: hidden;
}

.user-form__wrapper {
  max-width: 1245px;
  margin: 0 auto;
  padding: 91px 20px 113px 20px;
  display: flex;
}

.user-form__img-box {
  width: 47%;
  margin-left: -8px;
  padding-top: 70px;
  position: relative;
  z-index: 0;

  &::before {
    width: 671px;
    height: 641px;
    position: absolute;
    content: " ";
    top: -151px;
    left: -175px;
    background-image: url("../img/mask-plane-desktop@1x.png");
    background-repeat: no-repeat;
    z-index: -1;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/mask-plane-desktop@2x.png");
      background-size: 671px 641px;
    }
  }
}

.user-form__inner {
  width: 47.7%;
  margin-left: 54px;
  z-index: 1;
  position: relative;

  &::before {
    width: 265px;
    height: 255px;
    position: absolute;
    content: " ";
    top: -49px;
    right: -361px;
    background-image: url("../img/form-triangle@1x.png");
    background-repeat: no-repeat;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/form-triangle@2x.png");
      background-size: 265px 255px;
    }
  }

  &::after {
    width: 343px;
    height: 405px;
    position: absolute;
    content: " ";
    top: 196px;
    right: -374px;
    background-image: url("../img/form-triangle-big@1x.png");
    background-repeat: no-repeat;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/form-triangle-big@2x.png");
      background-size: 343px 405px;
    }
  }
}

.user-form__inner h2 {
  margin: 0;
  font-family: $additional-fonts;
  font-size: 36px;
  line-height: 51px;
  font-weight: 400;
  color: $additional-text-color;
}

.user-form__inner p {
  margin: 0;
  padding: 10px 0 51px 5px;
  color: $additional-text-color;
}

.user-form__box {
  margin-left: 5px;
}

.user-form__input-box {
  padding-bottom: 40px;
  display: flex;
  justify-content: space-between;
}

.user-form__input-box input {
  width: 48%;
  padding: 11px 15px 17px 15px;
  font-family: $base-fonts;
  font-size: 16px;
  line-height: 22px;
  background-color: rgba(255, 255, 255, 0.1);
  border: none;
  color: $additional-text-color;
  border-radius: 0;
  -webkit-appearance: none;

  &:last-child {
    margin-left: 30px;
  }

  &::placeholder {
    color: $additional-text-color;
  }
}

.user-form__box textarea {
  width: 100%;
  min-height: 154px;
  padding: 11px 15px;
  font-family: $base-fonts;
  font-size: 16px;
  line-height: 22px;
  border: none;
  color: $additional-text-color;
  background-color: rgba(255, 255, 255, 0.1);
  resize: none;
  overflow: auto;
  border-radius: 0;
  -webkit-appearance: none;

  &::placeholder {
    color: $additional-text-color;
  }
}

.user-form__box-agreement {
  padding-top: 38px;
  display: flex;
}

.user-form__box-agreement label {
  margin-left: 36px;
  padding-left: 33px;
  padding-top: 7px;
  font-family: $base-fonts;
  font-size: 14px;
  line-height: 16px;
  color: $additional-text-color;
  position: relative;
  display: block;
  cursor: pointer;

  &::before {
    width: 16px;
    height: 16px;
    position: absolute;
    content: " ";
    top: 8px;
    left: 1px;
    border: solid 1px $additional-text-color;
  }

  &:hover {
    opacity: 0.8;
  }
}

.user-form__box-agreement input[type="checkbox"]:focus + label {
  outline: rgb(59, 153, 252) auto 5px;
}

.user-form__box-agreement input:checked + label::after {
  width: 8px;
  height: 5px;
  position: absolute;
  content: " ";
  top: 13px;
  left: 5px;
  background-image: url("../img/icon-tick.svg");
  background-repeat: no-repeat;
}

/*----- Планшетная версия -----*/

@media (max-width: $tablet-max-width) {
  .user-form {
    width: auto;
    padding: 80px 85px 103px 87px;
  }

  .user-form__wrapper {
    width: auto;
    margin: 0;
    padding: 0;
  }

  .user-form__img-box {
    display: none;
  }

  .user-form__inner {
    width: 100%;
    margin-left: 0;
  }

  .user-form__inner h2 {
    font-size: 32px;
    line-height: 45px;
  }

  .user-form__inner p {
    padding: 16px 0 55px 6px;
  }

  .user-form__input-box input {
    padding: 11px 15px 19px 18px;
  }

  .user-form__box textarea {
    min-height: 160px;
    padding: 11px 17px;
  }

  .user-form__box-agreement label {
    margin-left: 39px;
    padding-left: 34px;
    padding-top: 9px;

    &::before {
      top: 10px;
      left: 1px;
    }
  }

  .user-form__box-agreement input:checked + label::after {
    top: 15px;
    left: 15px;
  }
}

/*----- Мобильная версия -----*/

@media (max-width: $mobile-max-width) {
  .user-form {
    padding: 30px 15px 42px 15px;
  }

  .user-form__inner h2 {
    font-size: 28px;
    line-height: 39px;
  }

  .user-form__inner p {
    padding: 13px 0 26px 0;
  }

  .user-form__box {
    margin-left: 0;
  }

  .user-form__input-box {
    padding: 0;
    flex-direction: column;
  }

  .user-form__input-box input {
    width: 100%;
    margin-bottom: 31px;

    &:last-child {
      margin: 0 0 30px 0;
    }
  }

  .user-form__box textarea {
    min-height: 103px;
  }

  .user-form__box-agreement {
    padding-top: 24px;
    flex-direction: column;
  }

  .user-form__box label {
    margin-left: 0;
    padding-left: 34px;
    padding-top: 30px;

    &::before {
      top: 31px;
    }
  }

  .user-form__box-agreement input:checked + label::after {
    top: 36px;
    left: 5px;
  }
}
