.advantages-list {
  @include mixin-list-reset;
  display: flex;
  flex-wrap: wrap;
  margin-left: 17px;
}

.advantages-list__item {
  width: 48%;
  min-height: 299px;
  margin-right: 30px;
  margin-bottom: 30px;
  border: solid 1px #ebebeb;
  border-left: none;
  box-shadow: inset 3px 0 0 $base-background-color;
  background-color: $additional-text-color;
  position: relative;

  &:nth-child(2n) {
    margin-right: 0;
  }

  &:hover {
    background-color: $base-background-color;
    color: $additional-text-color;
    cursor: pointer;

    path {
      fill: #ffffff;
    }

    h3 {
      color: #ffffff;
    }
  }
}

.advantages-list__item h3 {
  margin: 0;
  padding: 20px 0 16px 44px;
  font-family: $additional-fonts;
  font-size: 24px;
  line-height: 34px;
  font-weight: 400;
  color: $base-background-color;
}

.advantages-list__item p {
  margin: 0;
  padding: 0 35px 65px 45px;
}

.advantages-list__icon-watch {
  margin-top: 39px;
  margin-left: 45px;
  display: block;
}

.advantages-list__icon-provider {
  margin-top: 39px;
  margin-left: 44px;
  display: block;
}

.advantages-list__icon-incognito {
  margin-top: 43px;
  margin-left: 45px;
  display: block;
}

.advantages-list__icon-tag-price {
  margin-top: 39px;
  margin-left: 44px;
  display: block;
}

/*----- Планшетная версия -----*/

@media (max-width: $tablet-max-width) {
  .advantages-list {
    margin-left: 0;
  }

  .advantages-list__item {
    width: 47.9%;
  }

  .advantages-list__icon-watch {
    margin-top: 24px;
    margin-left: 28px;
  }

  .advantages-list__icon-provider {
    width: 31px;
    height: 37px;
    margin-top: 22px;
    margin-left: 30px;
  }

  .advantages-list__icon-incognito {
    width: 30px;
    height: 34px;
    margin-top: 22px;
    margin-left: 28px;
  }

  .advantages-list__icon-tag-price {
    width: 30px;
    height: 38px;
    margin-top: 21px;
    margin-left: 29px;
  }

  .advantages-list__item h3 {
    padding: 7px 0 7px 27px;
  }

  .advantages-list__item p {
    padding: 0 27px 42px 27px;
  }

  .advantages-list__item:nth-child(2n) p {
    padding-bottom: 20px;
  }

  .advantages-list__item:nth-child(3n) p {
    padding-top: 10px;
  }

  .advantages-list__item--height-size {
    min-height: 232px;
  }
}

/*----- Мобильная версия -----*/

@media (max-width: $mobile-max-width) {
  .advantages-list__item {
    width: 100%;
    min-height: 310px;
    margin-right: 0;
    margin-bottom: 20px;

    &:nth-child(3n) {
      min-height: 232px;
    }
  }

  .advantages-list__icon-provider {
    width: 32;
    height: 40px;
    margin-top: 20px;
    margin-left: 26px;
  }

  .advantages-list__icon-tag-price {
    height: 36px;
  }

  .advantages-list__item h3 {
    padding-top: 10px;
    padding-right: 16px;
    padding-bottom: 9px;
    font-size: 20px;
    line-height: 28px;
  }

  .advantages-list__item:nth-child(3n) h3 {
    padding-top: 7px;
  }

  .advantages-list__item p {
    padding: 0 14px 25px 26px;
  }

  .advantages-list__item:nth-child(2n) p {
    padding-bottom: 25px;
    padding-right: 13px;
  }

  .advantages-list__item:nth-child(3n) p {
    padding-top: 0;
  }

  .advantages-list__item:nth-child(4n) p {
    padding-right: 14px;
  }

  .advantages-list__item--height-size {
    min-height: 244px;
  }
}
