.social-list {
  @include mixin-list-reset;
  display: flex;
  flex-wrap: wrap;
}

.social-list__item {
  margin-right: 15px;
}

.social-list__icon-svg {
  &:hover {
    path {
      fill: rgba(102, 102, 102, 0.6);
    }
  }

  &:active {
    path {
      fill: rgba(102, 102, 102, 0.8);
    }
  }
}

/*----- Планшетная версия -----*/

@media (max-width: $tablet-max-width) {
  .social-list__item {
    margin-right: 14px;

    &:last-child {
      margin-right: 0;
    }
  }
}

/*----- Мобильная версия -----*/

@media (max-width: $mobile-max-width) {
  .social-list__item {
    &:first-child {
      margin-left: 0;
    }
  }
}
