.advantages {
  max-width: 1245px;
  margin: 0 auto;
  padding: 112px 20px 75px 20px;
  position: relative;

  &::before {
    width: 230px;
    height: 264px;
    position: absolute;
    content: " ";
    top: 44px;
    left: -135px;
    background-image: url("../img/advantages-img-mask@1x.png");
    background-repeat: no-repeat;
    z-index: -1;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/advantages-img-mask@2x.png");
      background-size: 230px 264px;
    }
  }

  &::after {
    width: 230px;
    height: 264px;
    position: absolute;
    content: " ";
    top: 50.8%;
    right: -255px;
    transform: rotate(60deg);
    background-image: url("../img/advantages-img-mask@1x.png");
    background-repeat: no-repeat;
    z-index: -1;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/advantages-img-mask@2x.png");
      background-size: 230px 264px;
    }
  }
}

/*----- Планшетная версия -----*/

@media (max-width: $tablet-max-width) {
  .advantages {
    width: auto;
    padding: 54px 25px 0 25px;

    &::before,
    &::after {
      display: none;
    }
  }
}

/*----- Мобильная версия -----*/

@media (max-width: $mobile-max-width) {
  .advantages {
    padding: 38px 15px 0 15px;
  }
}
