.menu-list {
  @include mixin-list-reset;
  width: 76%;
  height: 50%;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
}

.menu-list__item {
  margin-right: 20px;
}

.menu-list__item p {
  margin: 0;
  font-size: 15px;
  line-height: 25px;
  font-style: normal;
  color: rgba(27, 45, 55, 0.5);
}

.menu-list__item a {
  font-size: 15px;
  line-height: 25px;
  text-decoration: none;
  color: rgba(27, 45, 55, 0.5);

  &:hover {
    color: rgba(27, 45, 55, 0.2);
  }

  &:active {
    color: rgba(27, 45, 55, 0.1);
  }
}

.main-footer__address-wrapper .menu-list {
  width: auto;
  height: auto;
}

.main-footer__address-wrapper .menu-list .menu-list__item {
  margin-right: 0;
}

/*----- Планшетная версия -----*/

@media (max-width: $tablet-max-width) {
  .menu-list {
    width: auto;
    height: auto;
  }
}

/*----- Мобильная версия -----*/

@media (max-width: $mobile-max-width) {
  .menu-list {
    display: none;
    position: relative;
  }

  .main-footer__btn.main-footer__btn--active + .menu-list,
  .main-footer__btn.main-footer__btn--active + .main-footer__address-wrapper > .menu-list {
    display: block;
    padding-bottom: 26px;
  }
}
