.main-header {
  background-color: $additional-background-color;
  background-image: url("../img/bgck-mask-header@1x.png");
  background-repeat: no-repeat;
  background-size: cover;
  overflow: hidden;
  z-index: -5;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/bgck-mask-header@2x.png");
  }
}

.main-header__nav-wrapper {
  max-width: 1450px;
  margin: 0 auto;
  padding: 32px 20px 0 20px;
  padding-top: 32px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center;
}

.main-header__logo {
  display: flex;
  text-decoration: none;
  flex-grow: 3;
}

.main-header__text-block {
  padding: 9px 0 0 14px;
}

.main-header__text-block p {
  margin: 0;
  font-family: $additional-fonts-bold;
  font-size: 25px;
  line-height: 25px;
  color: $additional-text-color;
}

.main-header__text-block span {
  margin: 0;
  font-family: $additional-fonts-medium;
  font-size: 12px;
  line-height: 14px;
  color: #788891;
}

.main-header__site-list-wrapper {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  flex-grow: 1;
}

.main-header__content {
  max-width: 1450px;
  padding: 170px 20px 100px 14px;
  margin: 0 auto;
  display: flex;
}

.main-header__content-wrapper {
  position: relative;
}

.main-header__content-wrapper h2 {
  margin: 0;
  padding: 58px 0 0 0;
  font-family: $additional-fonts-bold;
  font-size: 80px;
  line-height: 94px;
  letter-spacing: 0.07em;
  color: $additional-text-color;
  text-transform: uppercase;
}

.main-header__content-wrapper p {
  margin: 0;
  padding: 27px 0 0 98px;
  position: relative;
  font-family: $base-fonts;
  font-size: 22px;
  line-height: 34px;
  color: $additional-text-color;

  &::before {
    width: 66px;
    height: 2px;
    position: absolute;
    content: " ";
    top: 43px;
    left: 4px;
    background-color: $additional-text-color;
  }
}

.main-header__content-wrapper span {
  position: absolute;
  content: " ";
  bottom: 24px;
  left: -35px;
  transform: rotate(-90deg);
}

.main-header__btn a {
  &:last-child {
    display: none;
  }
}

.main-header__advantages-scroll {
  font-size: 14px;
  line-height: 16px;
  color: rgba(255, 255, 255, 0.5);
  text-decoration: none;

  &:hover {
    color: rgba(255, 255, 255, 0.8);
  }
}

.main-header__img {
  margin-right: -78px;
  position: relative;
  z-index: 0;

  &::after {
    width: 625px;
    height: 525px;
    position: absolute;
    content: " ";
    top: 1px;
    left: 4px;
    background-image: url("../img/desktop-img-mask@1x.png");
    background-repeat: no-repeat;
    z-index: -1;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/desktop-img-mask@2x.png");
      background-size: 625px 505px;
    }
  }
}

/*----- Планшетная версия -----*/

@media (max-width: $tablet-max-width) {
  .main-header__nav-wrapper {
    width: auto;
    padding: 20px 25px 0 25px;
  }

  .main-header__icon-logo {
    margin-left: 0;
  }

  .main-header__text-block {
    padding-left: 16px;
  }

  .main-header__content {
    width: auto;
    padding: 0 25px 163px 25px;
  }

  .main-header__content-wrapper span a {
    display: none;
  }

  .main-header__img {
    margin-right: 0;

    &::after {
      width: 490px;
      height: 390px;
      top: 3px;
      left: -63px;
      background-image: url("../img/tablet-img-mask@1x.png");

      @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
        background-image: url("../img/tablet-img-mask@2x.png");
        background-size: 490px 390px;
      }
    }
  }

  .main-header__img img {
    width: 465px;
    height: 340px;
    margin: 38px 0 0 -8px;
  }

  .main-header__content h2 {
    padding: 113px 0 0 63px;
    font-size: 60px;
    line-height: 70px;
    letter-spacing: 4px;
  }

  .main-header__content p {
    padding: 29px 0 0 114px;
    font-size: 18px;
    line-height: 28px;

    &::before {
      width: 33px;
      top: 45px;
      left: 66px;
    }
  }
}

/*----- Мобильная версия -----*/

@media (max-width: $mobile-max-width) {
  .main-header__nav-wrapper {
    padding: 17px 15px 0 15px;
  }

  .main-header__icon-logo {
    width: 38px;
    height: 38px;
    margin-left: 2px;
  }

  .main-header__text-block {
    display: none;
  }

  .main-header__content {
    padding: 0 15px 70px 15px;
    flex-direction: column-reverse;
  }

  .main-header__content-wrapper {
    margin-top: -32px;
    display: flex;
    flex-direction: column;
    align-items: center;
    z-index: 1;
  }

  .main-header__content h2 {
    padding: 0;
    font-size: 40px;
    line-height: 47px;
    letter-spacing: 3px;
  }

  .main-header__content p {
    padding: 27px 0 0 51px;
    font-size: 16px;
    line-height: 25px;

    &::before {
      top: 42px;
      left: 3px;
    }
  }

  .main-header__btn a {
    &:first-child {
      display: none;
    }

    &:last-child {
      display: block;
    }
  }

  .main-header__img {
    display: flex;
    justify-content: center;

    &::after {
      width: 465px;
      height: 370px;
      top: 13px;
      left: 152px;
      background-image: url("../img/mobile-img-mask@1x.png");

      @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
        background-image: url("../img/mobile-img-mask@2x.png");
        background-size: 465px 370px;
      }
    }
  }

  .main-header__img img {
    width: 300px;
    height: 220px;
    margin: 37px 0 0 32px;
  }
}
