.button {
  width: 180px;
  padding: 13px 0;
  font-family: $additional-fonts;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  text-decoration: none;
  text-align: center;
  border: solid 1px $additional-text-color;
  border-radius: 5px;
  color: $additional-text-color;
  display: block;
  cursor: pointer;
  -webkit-appearance: none;

  &:hover {
    border: solid 1px rgba(255, 255, 255, 0.7);
    color: rgba(255, 255, 255, 0.7);
  }

  &:active {
    border: solid 1px rgba(255, 255, 255, 0.5);
    color: rgba(255, 255, 255, 0.5);
  }

  &--consultation {
    width: 407px;
    margin: 44px 0 0 98px;
    padding: 17px 0 19px 0;
    font-size: 16px;
    line-height: 19px;
    background-color: $base-background-color;
    border: none;

    &:hover {
      border: none;
      background-color: rgba(0, 178, 255, 0.7);
    }

    &:active {
      color: rgba(255, 255, 255, 0.5);
      background-color: rgba(0, 178, 255, 0.5);
    }
  }

  &--detailed {
    color: $base-background-color;
    border: solid 1px $base-background-color;

    &:hover {
      color: rgba(0, 178, 255, 0.6);
      border: solid 1px rgba(0, 178, 255, 0.6);
    }

    &:active {
      color: rgba(0, 178, 255, 0.4);
      border: solid 1px rgba(0, 178, 255, 0.4);
    }
  }

  &--question {
    width: 270px;
    background-color: transparent;

    &:hover {
      border: solid 1px rgba(255, 255, 255, 0.7);
      color: rgba(255, 255, 255, 0.7);
    }

    &:active {
      border: solid 1px rgba(255, 255, 255, 0.5);
      color: rgba(255, 255, 255, 0.5);
    }
  }

  &--modal-question {
    width: 100%;
    background-color: transparent;

    &:hover {
      border: solid 1px rgba(255, 255, 255, 0.7);
      color: rgba(255, 255, 255, 0.7);
    }

    &:active {
      border: solid 1px rgba(255, 255, 255, 0.5);
      color: rgba(255, 255, 255, 0.5);
    }
  }
}

/*----- Планшетная версия -----*/

@media (max-width: $tablet-max-width) {
  .button {
    &--consultation {
      width: 379px;
      margin: 55px 0 0 114px;
    }

    &--question {
      width: 280px;
      padding: 15px 0 14px 0;
    }
  }
}

/*----- Мобильная версия -----*/

@media (max-width: $mobile-max-width) {
  .button {
    &--consultation {
      width: 287px;
      margin: 40px 0 0 3px;
    }

    &--question {
      width: 100%;
    }
  }
}

/*----- Замыкание стилей на определённой ширине -----*/

@media (max-width: $tablet-width-only) {
  .button--call {
    display: none;
  }
}
